.quote-card {
  padding: 20px;
  border: 1px solid saddlebrown;
  background-color: wheat;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 15px;
}

.author {
  font-size: 14px;
  color: teal;
  text-transform: uppercase;
  font-weight: bold;
  margin-bottom: 10px;
}
.text {
  font-size: 24px;
  line-height: 1.6;
  letter-spacing: 1px;
}
